@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
}

body {
	font-family: "Nunito Sans", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-weight: 600;
	min-height: 100vh;
	font-size: 0.94rem;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.light > * {
	--primary-bg-white: rgb(255, 255, 255);
	--primary-bg2-white: rgb(250, 250, 250);
	--primary-color: black;
	--primary-border: rgba(165, 165, 165, 0.548);
	--primary-box-shadow: #5a5a5ac2;
}
.dark > * {
	--primary-bg-white: rgb(49, 59, 68);
	--primary-bg2-white: rgb(32, 45, 54);
	--primary-color: rgb(220, 220, 220);
	--primary-border: rgb(26, 26, 26);
	--primary-box-shadow: rgb(3, 3, 3);
}
.App {
	text-align: center;
	min-width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	background-color: var(--primary-bg2-white);
	color: var(--primary-color);
}

* {
	white-space: nowrap;
	word-wrap: break-word;
}

header {
	width: 100%;
	border-color: rgba(165, 165, 165, 0.548);
	border: 1px;
	box-shadow: 0px 0px 8px -4px rgba(63, 63, 63, 0.548);
	z-index: 10;
	background-color: var(--primary-bg-white);
}

main {
	background-color: var(--primary-bg2-white);
	width: 90%;
	min-height: 30rem;
	margin: 0 auto 2rem;
}

h4 {
	font-weight: 600;
}

li {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.container-search {
	background-color: var(--primary-bg2-white);
	box-shadow: unset;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

@media (min-width: 800px) {
	.container-search {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
}

.title {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 5.7rem;
	width: 90%;
	margin: 0 auto;
}

.title div:hover {
	cursor: pointer;
}

.moon {
	margin: 0 0.3rem;
	position: relative;
	bottom: -0.1rem;
}

form div {
	width: 100%;
	background-color: var(--primary-bg-white);
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 2rem;
	flex-direction: row;
	position: relative;
}

input {
	width: 100%;
	height: 3rem;
	border-color: rgba(165, 165, 165, 0.548);
	border: 1px;
	box-shadow: 0px 0px 16px -4px var(--primary-border);
	border-radius: 4px;
	font-size: 1rem;
	padding-left: 4.5rem;
	padding-right: 2rem;
	background-color: inherit;
	color: var(--primary-color);
}

input::-webkit-input-placeholder {
	color: rgba(184, 184, 184, 0.911);
	font-size: 0.97rem;
	margin-left: 2rem;
}

input:-ms-input-placeholder {
	color: rgba(184, 184, 184, 0.911);
	font-size: 0.97rem;
	margin-left: 2rem;
}

input::placeholder {
	color: rgba(184, 184, 184, 0.911);
	font-size: 0.97rem;
	margin-left: 2rem;
}

.search {
	background-color: inherit;
	height: 3rem;
	width: clamp(1rem, 6.5%, 2rem);
	color: var(--primary-color);
	position: absolute;
	left: 1.6rem;
	margin-right: 2rem;
}

@media (min-width: 800px) {
	form div {
		width: 150%;
	}
}

.filter {
	position: relative;
	z-index: 10;
	width: 12rem;
}
.filter:hover {
	cursor: pointer;
}

.select {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--primary-bg-white);
	width: 100%;
	padding: 1rem 0.8rem 1rem 1.4rem;
	margin-top: 2rem;
	margin-bottom: 2rem;
	border-color: rgba(165, 165, 165, 0.548);
	border: 1px;
	box-shadow: 0px 0px 6px -4px var(--primary-box-shadow);
	border-radius: 4px;
	font-size: 0.9rem;
}

.arrowDown {
	margin-right: 0;
	height: 1.2rem;
	width: 1.6rem;
}

.options {
	width: 100%;
	position: absolute;
	background-color: var(--primary-bg-white);
	top: 75%;
	padding: 1rem 0rem;
	text-align: left;
	margin-top: 0.3rem;
	border-color: var(--primary-border);
	border: 1px;
	box-shadow: 0px 0px 6px -4px var(--primary-box-shadow);
	font-size: 0.9rem;
	border-radius: 4px;
	display: grid;
	grid-gap: 0.4rem;
	gap: 0.4rem;
}

.options div {
	padding: 0 1.4rem;
}

.options div:hover {
	background-color: var(--primary-box-shadow);
}

.countries--container {
	display: flex;
	grid-gap: 4rem 0;
	gap: 4rem 0;
	flex-direction: column;
	justify-content: space-between;
	flex-wrap: wrap;
}

.country {
	width: 18rem;
	height: 20rem;
	border-radius: 8px;
	box-shadow: 0px 2px 8px -2px var(--primary-box-shadow);
	background-color: var(--primary-bg-white);
}

.country:hover {
	cursor: pointer;
}

.flag {
	height: 10rem;
	width: 100%;
	border-radius: 8px 8px 0 0;
	background-size: cover;
	object-fit: cover;
}

.country-stats {
	display: flex;
	flex-direction: column;
	grid-gap: 0.2rem;
	gap: 0.2rem;
	text-align: left;
	padding: 1.4rem;
	font-size: 1.1rem;
}

.country-stats:first-child {
	margin-bottom: 0.7rem;
}

li {
	list-style-type: none;
	font-weight: 400;
}
span {
	font-weight: 600;
}

@media (min-width: 800px) {
	.container--countries {
		flex-direction: row;
	}

	.country {
		width: 16rem;
	}
}

@media (min-width: 800px) {
	.countries--container {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-direction: row;
		flex-wrap: wrap;
		grid-gap: 8rem;
		gap: 8rem;
	}

	.country {
		width: 16rem;
	}
}

.details {
	width: 94%;
	height: 100%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	text-align: left;
}

.button-back {
	height: 2rem;
	width: 6rem;
	padding-right: 0.3rem;
	background-color: var(--primary-bg-white);
	box-shadow: 0px 0px 8px -1px var(--primary-box-shadow);
	color: var(--primary-color);
	font-size: 0.98rem;
	margin: 1.5rem 0 2rem;
	display: flex;
	justify-content: center;
	align-items: center;
	grid-gap: 0.2rem;
	gap: 0.2rem;
	border: none;
}
.button-back:hover {
	cursor: pointer;
}

.details--country--container {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100%;
}

.arrowLeft {
	height: 1.6rem;
	width: 1.8rem;
}

.flag-detail {
	width: 100%;
	height: 100%;
	background-size: cover;
	object-fit: cover;
	box-shadow: 0px 1px 6px -2px var(--primary-box-shadow);
}
.stats--container {
	width: 100%;
	text-align: left;
	padding: 2rem 0;
}

.stats--container h2 {
	font-size: 1.64rem;
}
.stats--inner--container {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	grid-gap: 2rem;
	gap: 2rem;
	padding-top: 1.8rem;
}
.stats {
	min-width: 45%;
	display: flex;
	flex-direction: column;
	grid-gap: 0.4rem;
	gap: 0.4rem;
	overflow: hidden;
	text-overflow: ellipsis;
	word-wrap: break-word;
	word-break: break-all;
}

.detail-stats li span {
	margin-right: 0.2rem;
}

.stats li {
	word-break: keep-all;
	word-wrap: break-word;
	white-space: normal;
}

.detail-stats-three {
	display: flex;
	flex-direction: column;
	padding-top: 1rem;
	width: 100%;
	grid-gap: 0.7rem;
	gap: 0.7rem;
}

.border-countries {
	font-weight: 600;
	font-size: 1.1rem;
	/* display: grid;
	grid-template-columns: repeat(auto-fill, minmax(30%, 1fr)); */
	display: flex;
	flex-wrap: wrap;
	grid-gap: 0.4rem;
	gap: 0.4rem;
	width: 100%;
}

.border-countries li {
	display: grid;
	place-items: center;
	padding: 0.2rem 0.6rem;
	font-size: 0.78rem;
	font-weight: 600;
	overflow: hidden;
	box-shadow: 0px 0px 5px -1px var(--primary-box-shadow);
	background-color: var(--primary-bg-white);
}

.border-countries li:hover {
	cursor: pointer;
}

@media (min-width: 800px) {
	.details {
		width: 100%;
	}
	.button-back {
		height: 2.2rem;
		width: 8rem;
		margin: 2.5rem 0 3rem;
	}

	.details--country--container {
		flex-direction: row;
	}

	.flag-detail {
		width: 46%;
		height: 100%;
	}

	.stats--container {
		width: 50%;
		padding: 1.6rem 0 0 1.6rem;
	}

	.stats--container h2 {
		font-size: 2rem;
	}

	.detail-stats li span {
		margin-right: 0.2rem;
	}

	.detail-stats-three {
		flex-direction: row;
	}

	.border-countries li {
		padding: 0.2rem 0.8rem;
	}
}

